.profile__avatar {
  border-radius: 50%;
  max-width: 120px;
  margin: 40px 30px 50px 0;
  object-fit: cover;
  object-position: bottom;
  width: 120px;
  height: 120px;
}

@media screen and (max-width: 1150px) {
  .profile__avatar {
    margin: 40px 30px 0 0;
  }
}

@media screen and (max-width: 900px) {
  .profile__avatar {
    margin: 0 0 26px 0;
  }
}