.popup__input {
  width: 358px;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  border: 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  padding-bottom: 9px;
}

@media screen and (max-width: 520px) {
  .popup__input {
    width: 238px;
  }
}

.popup__input:focus {
  outline: solid  2px rgb(81, 141, 214);
}